import { defHttp } from '/@/utils/http/axios';
import { BasicColumn } from '/@/components/Table';
import type { FormProps } from '/@/components/Form/src/types/form';

enum Api {
  FUNCTION_TREE_URL = '/system/function/list-tree',
  CREATE_URL = '/oa/work-menu/create',
  UPDATE_URL = '/oa/work-menu/update',
  DELETE_URL = '/oa/work-menu/delete',
  GET_URL = '/oa/work-menu/get',
  PAGE_URL = '/oa/work-menu/page',
  TABLE_NAME_PAGE_URL = '/oa/work-menu/getTableNamePage',
}

// 获得表名分页
export const getTablePageUrl = Api.TABLE_NAME_PAGE_URL;
export const getTableNamePage = (params) =>
  defHttp.get(
    {
      url: Api.TABLE_NAME_PAGE_URL,
      params,
    },
    { isOnlyResult: true },
  );

// 菜单树
export const getTreeData = (params) =>
  defHttp.post(
    {
      url: Api.FUNCTION_TREE_URL,
      params,
    },
    { isOnlyResult: true },
  );

// 新增工作菜单
export const createData = (params) =>
  defHttp.post(
    {
      url: Api.CREATE_URL,
      params,
    },
    {},
  );

// 修改工作菜单
export const updateData = (params) =>
  defHttp.put(
    {
      url: Api.UPDATE_URL,
      params,
    },
    {},
  );

export const saveData = (params) => {
  if (!params.id) {
    return createData(params);
  }
  return updateData(params);
};

// 删除工作菜单
export const deleteData = (id: string) =>
  defHttp.delete(
    {
      url: `${Api.DELETE_URL}?id=${id}`,
    },
    {},
  );

// 获取工作菜单数据
export const getData = (id: string) =>
  defHttp.get(
    {
      url: `${Api.GET_URL}?id=${id}`,
    },
    { isOnlyResult: true },
  );

// 分页获取工作菜单列表
export const getPageUrl = Api.PAGE_URL;
export const getPage = (params) =>
  defHttp.get(
    {
      url: Api.PAGE_URL,
      params,
    },
    { isOnlyResult: true },
  );

